import React, { Fragment } from "react";
import { makeStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import { Switch, Route, Link, HashRouter } from "react-router-dom";
import Button from "@material-ui/core/Button";
import Box from "@material-ui/core/Box";
import Home from "./components/HomeComponents/Home";
import Project from "./components/ProjectComponents/Project";
import Contact from "./components/Contact/Contact";
import Paper from '@material-ui/core/Paper'
import Avatar from '@material-ui/core/Avatar';

const useStyles = makeStyles((theme) => ({
  header: {
    flexGrow: 1,
  },
  boxLeft: {
    paddingLeft: "70px",
  },
  boxRight: {
    paddingRight: "70px",
  },
  large: {
    width: theme.spacing(7),
    height: theme.spacing(7),
  },
}));

export default () => {
  const classes = useStyles();

  return (
    <HashRouter>
      <div>
        <Route
          path="/"
          render={() => (
            <Fragment>

              <AppBar position="static" className={classes.header} elevation={0}>

                <Toolbar>

                  <div style={{ width: "100%" }}>
                    <Paper>
                      <Box display="flex" p={1} className={classes.boxLeft}>
                        <Box p={1} flexGrow={1}>
                          <Avatar alt="Developer" className={classes.large} src={require('./assets/programmer.svg')}/>
                        </Box>
                        <Box p={1} color="secondary">
                          <Button component={Link} to="/test">
                            Home
                        </Button>
                        </Box>
                        <Box p={1} color="secondary">
                          <Button component={Link} to="/test/project">
                            Project
                        </Button>
                        </Box>
                        <Box p={1} color="secondary" className={classes.boxRight}>
                          <Button component={Link} to="/test/about">
                            Contact
                        </Button>
                        </Box>
                      </Box>
                    </Paper>
                  </div>

                </Toolbar>

              </AppBar>

              <Switch>
                <Route
                  exact
                  path="/test"
                  render={() => (
                    <div>
                      <Home />
                    </div>
                  )}
                />
                <Route
                  exact
                  path="/test/project"
                  render={() => (
                    <div>
                      <Project />
                    </div>
                  )}
                />
                <Route
                  exact
                  path="/test/about"
                  render={() => (
                    <div>
                      <Contact />
                    </div>
                  )}
                />
              </Switch>
            </Fragment>
          )}
        />
      </div>
    </HashRouter>
  );
};
